import { Component, NgModuleRef, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { RxFormBuilder, RxwebValidators } from '@rxweb/reactive-form-validators';
import { Observable } from 'rxjs';
import { AlertBoxStyle } from '../../common/alert-box/alert-box.component';
import { CaptchaComponent } from '../../common/captcha/captcha.component';
import { CommandBarButton, CommandBarButtonStyle, CommandBarButtonType } from '../../common/command-bar/command-bar.component';
import { SplashScreenConfig, SplashScreenPosition, SplashScreenStatus } from '../../common/splash-screen/splash-screen.component';
import { IrdValidators } from '../../common/validator/ird-validator';
import { HeartbeatService } from '../../heartbeat/heartbeat.service';
import { IdType } from '../../model/IdType';
import { AlertBoxService } from '../../service/alert-box.service';
import { PortalService } from '../../service/portal.service';
import { ScheduleService } from '../../service/schedule/schedule.service';
import { RegistrationService } from '../service/registration.service';

@Component({
    selector: 'app-step3',
    templateUrl: './step3.component.html',
    styleUrls: ['./step3.component.sass']
})
export class Step3Component implements OnInit {

    scopeName = 'portal.Registration.Step3';

    readonly IdType = IdType;

    userInfoForm: FormGroup;

    commandBarButtons: CommandBarButton[] = [];

    splashScreenConfig: SplashScreenConfig;

    @ViewChild(CaptchaComponent)
    private _captchaComponent: CaptchaComponent;

    constructor(
        private readonly _portalService: PortalService,
        private readonly _router: Router,
        private readonly _heartbeatService: HeartbeatService,
        private readonly _registrationService: RegistrationService,
        private readonly _scheduleService: ScheduleService,
        private readonly _formBuilder: RxFormBuilder,
        private readonly _alertBoxService: AlertBoxService,
        private readonly _ngModuleRef: NgModuleRef<Step3Component>) {

        this.splashScreenConfig = {
            position: SplashScreenPosition.Fixed,
            message: {
                scopeName: this.scopeName,
                label: 'loading'
            },
            visibility: false,
            status: SplashScreenStatus.Loading
        }

        this._portalService.setPageTitle(this.scopeName + '.title');

        this.commandBarButtons.push({
            type: CommandBarButtonType.SubmitButton,
            content: {
                scopeName: this.scopeName,
                label: 'btn.continue'
            },
            style: CommandBarButtonStyle.Positive,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onContinueButtonClick();
                    observer.next();
                })
                return observable;
            }
        },{
            content: {
                scopeName: this.scopeName,
                label: 'btn.abort'
            },
            style: CommandBarButtonStyle.Normal,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onAbortButtonClick();
                    observer.next();
                })
                return observable;
            }
        });
    }

    ngOnInit() {
        this._registrationService.reset();
        this._scheduleService.reset();

        this.userInfoForm = this._formBuilder.group({
            idType: ['', RxwebValidators.required({ message: '0001' })],
            firstChar: ['', RxwebValidators.compose(
                                { conditionalExpression: (x, y) => y.idType === IdType.HKIC,
                                  validators: [RxwebValidators.required({message: '0002'}),
                                               RxwebValidators.pattern({expression: {onlyAlpha: /^[A-Za-z]+$/}, message: '0003'}),
                                               RxwebValidators.maxLength({message: '0004', value: 2})]
                                }
                            )
                        ],
            middleDigits: ['', RxwebValidators.compose(
                                { conditionalExpression: (x, y) => y.idType === IdType.HKIC,
                                  validators: [RxwebValidators.required({message: '0005'}),
                                               RxwebValidators.digit({message: '0006'}),
                                               RxwebValidators.minLength({message: '0007', value: 3}),
                                               RxwebValidators.maxLength({message: '0007', value: 3})]
                                }
                            )
                        ],
            checkDigit: ['', RxwebValidators.compose(
                                { conditionalExpression: (x, y) => y.idType === IdType.HKIC,
                                  validators: [RxwebValidators.required({message: '0008'}),
                                               RxwebValidators.pattern({expression: {idCheckDigit: /^[0-9A]+$/}, message: '0009'}),
                                               RxwebValidators.maxLength({message: '0010', value: 1})]
                                }
                            )
                        ],
            passport: ['', RxwebValidators.compose(
                                { conditionalExpression: (x, y) => y.idType === IdType.Passport,
                                  validators: [RxwebValidators.required({message: '0011'}),
                                               RxwebValidators.alphaNumeric({message: '0012'}),
                                               RxwebValidators.minLength({message: '0013', value: 5}),
                                               RxwebValidators.maxLength({message: '0013', value: 5})]
                                }
                            )
                        ],
            surname: ['', [
                            RxwebValidators.required({message: '0014'}),
                            IrdValidators.text({
                                char: { message: '0015' },
                                english: { message: '0016', maxLength: 15 },
                                chinese: { message: '0017', maxLength: 2 }
                            })
                        ]
                    ]
        });
    }

    onLoadCaptchaStarted() {
        this.splashScreenConfig.visibility = true;
    }

    onLoadCaptchaCompleted(succeeded: boolean) {
        // disable the continue button when failed to load captcha
        this.commandBarButtons[0].disabled = !succeeded;

        this.splashScreenConfig.visibility = false;
    }

    onAbortButtonClick() {
        this._router.navigate(['/landing/welcome']);
    }

    onContinueButtonClick() {
        if (!this.userInfoForm.valid) {
            return;
        }

        this.splashScreenConfig.visibility = true;

        let broswerInfo = this._portalService.getBrowserInfo();

        let formValues = this.userInfoForm.value;

        const id = (formValues['idType'] === IdType.HKIC ? (formValues['firstChar'] + formValues['middleDigits'] + formValues['checkDigit']) : formValues['passport']);
        const request = {
            applicant : {
                id: id,
                idType: formValues['idType'],
                surname: formValues['surname']
            },
            captcha: this._captchaComponent.captchaForm.value.captcha,
            sessionId: this._captchaComponent.captcha.sessionId
        };

        this._registrationService.enroll(request).subscribe({
            next: () => {
                this._heartbeatService.start();
                this._router.navigate(['/registration/step4']);
            },
            error: (errorCode: string) => {
                this.splashScreenConfig.visibility = false;
                this._alertBoxService.show(this._ngModuleRef.componentFactoryResolver, {
                    style: AlertBoxStyle.Error,
                    buttons: [{ label: 'close' }],
                    message: {
                        scopeName: this.scopeName + '.ErrorMsg' ,
                        label: errorCode
                    }
                });
            }
        });
    }
}
