import { AbstractControl, ValidationErrors, ValidatorFn } from "@angular/forms";
import { StringUtility } from '../../shared/string-utility';
import { ValidatorUtility } from './validator-utility';

export interface TextValidatorConfig {
    english: {
        messageKey?: string,
        message?: string,
        maxLength: number
    },
    chinese: {
        messageKey?: string,
        message?: string,
        maxLength: number
    },
    char: {
        messageKey?: string,
        message?: string
    }
}

/**
 * Text validator
 * @param config 
 */
export function TextValidator(config: TextValidatorConfig): ValidatorFn {
    return (control: AbstractControl): ValidationErrors => {
        let text: string = control.value;

        if (StringUtility.isNullOrEmpty(text)) {
            return null;
        }

        if (isEnglishOnly(text)) {
            if (text.length > config.english.maxLength) {
                return ValidatorUtility.getValidationErrors('english', config.english);
            }
        } else if (isChineseOnly(text)) {
            if (getChineseTextLength(text) > config.chinese.maxLength) {
                return ValidatorUtility.getValidationErrors('chinese', config.chinese);
            }
        } else {
            return ValidatorUtility.getValidationErrors('char', config.char);
        }

        return null;
    }
}

function isEnglishOnly(input: string): boolean {
    return null != input.match(/^[A-Za-z ]+$/);
}

function isChineseOnly(input: string): boolean {
    return null != input.match(/^(?:[\u3400-\u4DBF\u4E00-\u9FFF]|[\uD840-\uD868\uD86A-\uD879\uD880-\uD883][\uDC00-\uDFFF]|\uD869[\uDC00-\uDEDF\uDF00-\uDFFF]|\uD87A[\uDC00-\uDFEF]|\uD87E[\uDC00-\uDE1F]|\uD884[\uDC00-\uDF4F])+$/);
}

function getChineseTextLength(input: string): number {
    /**
     * String.length cannot correctly return the length of a string
     * containing characters represented as surrogate pairs
     * Reference: https://mathiasbynens.be/notes/javascript-unicode
     */
    let multibyteChars = input.match(/[\uD800-\uDBFF][\uDC00-\uDFFF]/gm);
    let singleByteChars = input.replace(/[\uD800-\uDBFF][\uDC00-\uDFFF]/gm, '').split('');

    if (multibyteChars === null) {
        return singleByteChars.length;
    } else {
        return multibyteChars.concat(singleByteChars).length;
    }
}
