import { Component, NgModuleRef, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { RxFormArray, RxFormBuilder, RxFormGroup, RxwebValidators } from '@rxweb/reactive-form-validators';
import { Observable } from 'rxjs';
import { AlertBoxStyle } from '../../common/alert-box/alert-box.component';
import { AlertBoxService } from '../../service/alert-box.service';
import { CommandBarButton, CommandBarButtonStyle } from '../../common/command-bar/command-bar.component';
import { HeartbeatService } from '../../heartbeat/heartbeat.service';
import { PortalService } from '../../service/portal.service';
import { ScheduleService } from '../../service/schedule/schedule.service';
import { RegistrationService } from '../service/registration.service';
import { IrdValidators } from '../../common/validator/ird-validator';
import { BusinessName } from '../../model/businessName';

@Component({
    selector: 'app-step5',
    templateUrl: './step5.component.html',
    styleUrls: ['./step5.component.sass']
})
export class Step5Component implements OnInit {

    scopeName = 'portal.Registration.Step5';

    commandBarButtons: CommandBarButton[] = [];

    businessNameForm: FormGroup;

    get numberOfCases(): number {
        return this._scheduleService.selectedSchedule.numberOfCases;
    }

    constructor(
        private readonly _portalService: PortalService,
        private readonly _router: Router,
        private readonly _formBuilder: RxFormBuilder,
        private readonly _scheduleService: ScheduleService,
        private readonly _registrationService: RegistrationService,
        private readonly _heartbeatService: HeartbeatService,
        private readonly _alertBoxService: AlertBoxService,
        private readonly _ngModuleRef: NgModuleRef<Step5Component>) {

        this._portalService.setPageTitle(this.scopeName + '.title');

        this.commandBarButtons.push({
            content: {
                scopeName: this.scopeName,
                label: 'btn.continue'
            },
            style: CommandBarButtonStyle.Positive,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onContinueButtonClick();
                    observer.next();
                })
                return observable;
            }
        },{
            content: {
                scopeName: this.scopeName,
                label: 'btn.back'
            },
            style: CommandBarButtonStyle.Normal,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onBackButtonClick();
                    observer.next();
                })
                return observable;
            }
        },{
            content: {
                scopeName: this.scopeName,
                label: 'btn.abort'
            },
            style: CommandBarButtonStyle.Normal,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onAbortButtonClick();
                    observer.next();
                })
                return observable;
            }
        });

        this._heartbeatService.register(_ngModuleRef.componentFactoryResolver);

        //form array
        //https://gitter.im/rxweb-project/rxweb?at=5d4c439690bba62a123df7f6
        //https://gitter.im/rxweb-project/rxweb?at=5dd725785b3db9548acaa8f1
        // this.businessNameForm = this._formBuilder.group({
        //     businessNames:  new RxFormArray([], [this.buildBusinessNameFormGroup(0)])
        // }) as RxFormGroup;

        let businessNameControls = [];
        this._registrationService.appointment.registrations.forEach(x => {
            businessNameControls.push(this.buildBusinessNameFormGroup(x.seqNo, x.businessName));
        });

        if (businessNameControls.length === 0) {
            businessNameControls.push(this.buildBusinessNameFormGroup(1));
        }

        this.businessNameForm = this._formBuilder.group({
            businessNames: new RxFormArray([], businessNameControls)
        }) as RxFormGroup;
    }

    ngOnInit() {
        this._heartbeatService.start();
    }

    private buildBusinessNameFormGroup(seqNo: number, businessName: string = ''): FormGroup {
        return this._formBuilder.group({
            seqNo: [seqNo],
            businessName: [businessName, [RxwebValidators.required({message: '0022'}),
                                RxwebValidators.unique({message: '0023'}),
                                IrdValidators.text({
                                    char: {
                                        message: '0024'
                                    },
                                    english: {
                                        message: '0025',
                                        maxLength: 120
                                    },
                                    chinese: {
                                        message: '0026',
                                        maxLength: 30
                                    }
                                })]
                            ]
        }) as RxFormGroup;
    }

    onAbortButtonClick() {
        this._portalService
            .terminateUserSession()
            .subscribe({
                next: () => {
                    // stop the heartbeat service
                    this._heartbeatService.stop();
                    this._router.navigate(['/landing/welcome']);
                },
                error: (errorCode: string) => {
                    this._alertBoxService.show(this._ngModuleRef.componentFactoryResolver, {
                        style: AlertBoxStyle.Error,
                        buttons: [ { label: 'close' }],
                        message: {
                            scopeName: this.scopeName,
                            label: errorCode
                        }
                    })
                }
            });
    }

    onBackButtonClick() {
        this._registrationService.appointment.registrations.length = 0;

        (this.businessNameForm.controls.businessNames.value as BusinessName[]).forEach(x => {
            this._registrationService.appointment.registrations.push(x);
        });

        this._router.navigate(['/registration/step4']);
    }

    onContinueButtonClick() {
        if (this.businessNameForm.invalid) {
            return;
        }

        this._registrationService.appointment.registrations.length = 0;

        (this.businessNameForm.controls.businessNames.value as BusinessName[]).forEach(x => {
            this._registrationService.appointment.registrations.push(x);
        });
        this._router.navigate(['/registration/step6']);
    }

    get businessNames(): RxFormArray {
        return this.businessNameForm.get('businessNames') as RxFormArray
    }

    addBusinessName() {
        if (this.businessNames.length < this.numberOfCases) {
            this.businessNames.push(this.buildBusinessNameFormGroup(this.businessNames.length + 1));
        }
    }

    removeCase(seqNo: number) {
        if (this.businessNames.length > 1) {
            this.businessNames.removeAt(seqNo);
            for (let i = 0; i < this.businessNames.length; i++) {
                this.businessNames.controls[i].patchValue({seqNo:  i + 1})
            }
        }
    }
}
