import { Component, NgModuleRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { AlertBoxStyle } from '../../common/alert-box/alert-box.component';
import { CommandBarButton, CommandBarButtonStyle } from '../../common/command-bar/command-bar.component';
import { SplashScreenConfig, SplashScreenPosition, SplashScreenStatus } from '../../common/splash-screen/splash-screen.component';
import { TimeslotComponent } from '../../common/timeslot/timeslot.component';
import { HeartbeatService } from '../../heartbeat/heartbeat.service';
import { Meridiem } from '../../model/Meridiem';
import { Schedule } from '../../model/Schedule';
import { Timeslot } from '../../model/timeslot';
import { AlertBoxService } from '../../service/alert-box.service';
import { PortalService } from '../../service/portal.service';
import { ScheduleService } from '../../service/schedule/schedule.service';
import { RESULT_CODE_SUCCESS } from '../../shared/constant';

@Component({
    selector: 'app-step4',
    templateUrl: './step4.component.html',
    styleUrls: ['./step4.component.sass']
})
export class Step4Component implements OnInit {

    scopeName = 'portal.Registration.Step4';

    Meridiem = Meridiem;

    @ViewChild(TimeslotComponent)
    private timeslotComponent: TimeslotComponent;

    commandBarButtons: CommandBarButton[] = [];

    splashScreenConfig: SplashScreenConfig;

    get selectedAppointmentDate(): Schedule {
        return this._scheduleService.selectedSchedule;
    }
    get selectedAppointmentTimeslot(): Timeslot {
        return this._scheduleService.selectedTimeslot;
    }

    constructor(
        private readonly _portalService: PortalService,
        private readonly _router: Router,
        private readonly _heartbeatService: HeartbeatService,
        private readonly _alertBoxService: AlertBoxService,
        private readonly _ngModuleRef: NgModuleRef<Step4Component>,
        private readonly _scheduleService: ScheduleService) {

        this.splashScreenConfig = {
            position: SplashScreenPosition.Fixed,
            message: {
            scopeName: this.scopeName,
            label: 'loading'
            },
            visibility: false,
            status: SplashScreenStatus.Loading
        }

        this._portalService.setPageTitle(this.scopeName + '.title');

        this.commandBarButtons.push({
            content: {
                scopeName: this.scopeName,
                label: 'btn.continue'
            },
            style: CommandBarButtonStyle.Positive,
            hidden: false,
            disabled: true,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onContinueButtonClick();
                    observer.next();
                })
                return observable;
            }
        }, {
            content: {
                scopeName: this.scopeName,
                label: 'btn.abort'
            },
            style: CommandBarButtonStyle.Normal,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onAbortButtonClick();
                    observer.next();
                })
                return observable;
            }
        });

        this._heartbeatService.register(_ngModuleRef.componentFactoryResolver);
    }

    ngOnInit() { }

    onAbortButtonClick() {
        this._portalService
            .terminateUserSession()
            .subscribe({
                next: () => {
                    // stop the heartbeat service
                    this._heartbeatService.stop();
                    this._router.navigate(['/landing/welcome']);
                },
                error: (errorCode: string) => {
                    this._alertBoxService.show(this._ngModuleRef.componentFactoryResolver, {
                        style: AlertBoxStyle.Error,
                        buttons: [ { label: 'close' }],
                        message: {
                            scopeName: this.scopeName + '.ErrorMsg',
                            label: errorCode
                        }
                    })
                }
            });
    }

    onContinueButtonClick() {
        this._router.navigate(['/registration/step5']);
    }

    onLoadSchedulesStarted() {
        this.splashScreenConfig.visibility = true;
    }

    onLoadSchedulesCompleted(resultCode: string) {
        this.splashScreenConfig.visibility = false;

        if (resultCode != RESULT_CODE_SUCCESS) {
            this._alertBoxService.show(this._ngModuleRef.componentFactoryResolver, {
                style: AlertBoxStyle.Error,
                buttons: [{ label: 'close' }],
                message: {
                    scopeName: this.scopeName + '.ErrorMsg',
                    label: resultCode
                }
            });
        }

        this.commandBarButtons[0].disabled = !this.selectedAppointmentDate || !this.selectedAppointmentTimeslot;
    }

    onLoadTimeslotsStarted() {
        this.splashScreenConfig.visibility = true;
    }

    onLoadTimeslotCompleted(resultCode: string) {
        this.splashScreenConfig.visibility = false;

        if (resultCode != RESULT_CODE_SUCCESS) {
            this._alertBoxService.show(this._ngModuleRef.componentFactoryResolver, {
                style: AlertBoxStyle.Error,
                buttons: [{ label: 'close' }],
                message: {
                    scopeName: this.scopeName + '.ErrorMsg',
                    label: resultCode
                }
            });
        }

        this.commandBarButtons[0].disabled = !this.selectedAppointmentDate || !this.selectedAppointmentTimeslot;
    }

    getFormattedTime(timeValue: number) {
        if (timeValue > 1200) {
            timeValue -= 1200;
        }
        let formattedTime = timeValue.toString().length === 4 ? timeValue.toString() : '0' + timeValue;
        return formattedTime.substring(0, 2) + ':' + formattedTime.substring(2, 4);
    }

}
