import { ComponentFactoryResolver, NgModule } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { CoalescingComponentFactoryResolver } from 'src/app/portal/service/coalescing-component-factory-resolver.service';
import { PortalCommonModule } from '../common/portal-common.module';
import { DirectAccessGuard } from '../route-guard/direct-access.guard';
import { LanguageService } from '../service/language.service';
import { ScheduleService } from '../service/schedule/schedule.service';
import { RegistrationRoutingModule } from './registration-routing.module';
import { RegistrationService } from './service/registration.service';
import { Step3Component } from './step3/step3.component';
import { Step4Component } from './step4/step4.component';
import { Step5Component } from './step5/step5.component';
import { Step6Component } from './step6/step6.component';
import { Step7Component } from './step7/step7.component';

@NgModule({
    declarations: [
        Step3Component,
        Step4Component,
        Step5Component,
        Step6Component,
        Step7Component
    ],
    imports: [
        PortalCommonModule,

        RegistrationRoutingModule
    ],
    providers: [
        ScheduleService,
        RegistrationService,
        DirectAccessGuard
    ]
})
export class RegistrationModule {
    constructor(private _translateService: TranslocoService,
        private _languageService: LanguageService,
        private _coalescingResolver: CoalescingComponentFactoryResolver,
        private _localResolver: ComponentFactoryResolver) {

        this._coalescingResolver.registerResolver(this._localResolver);
        this._languageService.register(this._translateService);
    }
}
