import { ValidationErrors } from '@angular/forms';

export interface ValidatorConfig {
    messageKey?: string,
    message?: string
}

export class ValidatorUtility {
    public static getValidationErrors(validity: string, config: ValidatorConfig, errors: ValidationErrors = null): ValidationErrors {
        if (!errors) {
            errors = {};
        }
    
        let messageKey = config?.messageKey? config.messageKey : validity;
    
        errors[messageKey] = true;
    
        if (config?.message) {
            errors[messageKey] = {
                message: config.message
            }
        }
    
        return errors;
    }
} 
