import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { saveAs } from 'file-saver';
import htmlToImage from 'html-to-image';
import { Observable } from 'rxjs';
import { CommandBarButton, CommandBarButtonStyle } from '../../common/command-bar/command-bar.component';
import { SplashScreenConfig, SplashScreenPosition, SplashScreenStatus } from '../../common/splash-screen/splash-screen.component';
import { Appointment } from '../../model/Appointment';
import { IdType } from '../../model/IdType';
import { Meridiem } from '../../model/Meridiem';
import { PortalService } from '../../service/portal.service';
import { RegistrationService } from '../service/registration.service';

@Component({
    selector: 'app-step7',
    templateUrl: './step7.component.html',
    styleUrls: ['./step7.component.sass']
})
export class Step7Component implements OnInit {

    scopeName = 'portal.Registration.Step7';

    readonly IdType = IdType;
    readonly Meridiem = Meridiem;

    get appointment(): Appointment {
        return this._registrationService.appointment;
    }

    commandBarButtons: CommandBarButton[] = [];

    @ViewChild('acknowledgement')
    acknowledgement: ElementRef;

    splashScreenConfig: SplashScreenConfig;

    constructor(
        private readonly _portalService: PortalService,
        private readonly _router: Router,
        private readonly _registrationService: RegistrationService) {

        this._portalService.setPageTitle(this.scopeName + '.title');

        this.splashScreenConfig = {
            position: SplashScreenPosition.Fixed,
            message: {
                scopeName: this.scopeName,
                label: 'loading'
            },
            visibility: false,
            status: SplashScreenStatus.Loading
        }

        this.commandBarButtons.push({
            content: {
                scopeName: this.scopeName,
                label: 'btn.print'
            },
            style: CommandBarButtonStyle.Normal,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onPrintButtonClick();
                    observer.next();
                })
                return observable;
            }
        },{
            content: {
                scopeName: this.scopeName,
                label: 'btn.save'
            },
            style: CommandBarButtonStyle.Normal,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onSaveButtonClick();
                    observer.next();
                })
                return observable;
            }
        },{
            content: {
                scopeName: this.scopeName,
                label: 'btn.exit'
            },
            style: CommandBarButtonStyle.Normal,
            hidden: false,
            disabled: false,
            action: () => {
                let observable = new Observable<any>((observer) => {
                    this.onExitButtonClick();
                    observer.next();
                })
                return observable;
            }
        });
    }

    ngOnInit() { }

    onExitButtonClick() {
        this._router.navigate(['/landing/welcome']);
    }

    onSaveButtonClick() {
        this.splashScreenConfig.visibility = true;
        htmlToImage.toPng(this.acknowledgement.nativeElement).then(dataUri => {
            saveAs(dataUri, `${this.appointment.trn}.png`);

            this.splashScreenConfig.visibility = false;
        });
    }

    onPrintButtonClick() {
        window.print();
    }

    getFormatedHKIC(id: string) {
        const lastCharLength = id.length-1;
        return id.substr(0, lastCharLength) + 'XXX(' + id.charAt(lastCharLength) + ')';
    }

    getFormattedTime(timeValue: number) {
        if (timeValue > 1200) {
            timeValue -= 1200;
        }
        let formattedTime = timeValue.toString().length === 4 ? timeValue.toString() : '0' + timeValue;
        return formattedTime.substring(0, 2) + ':' + formattedTime.substring(2, 4);
    }
}
