import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { ConfigService } from '@ngx-config/core';
import { SESSION_STORAGE, WebStorageService } from 'ngx-webstorage-service';
import { Observable } from 'rxjs/internal/Observable';
import { Appointment } from '../../model/Appointment';
import { ServiceType } from '../../model/ServiceType';
import { EnrollRequest } from '../../service/request/EnrollRequest';
import { EnrollResponse } from '../../service/response/EnrollResponse';
import { ScheduleService } from '../../service/schedule/schedule.service';
import { HEADERS_AJAX_JSON_REQUEST, RESULT_CODE_SUCCESS, SESSION_DATA_KEY_JWT } from '../../shared/constant';
import { SubmitRegistrationRequest } from './request/SubmitRegistrationRequest';
import { SubmitRegistrationResponse } from './response/SubmitRegistrationResponse';

@Injectable()
export class RegistrationService {

    appointment: Appointment;

    constructor(
        private readonly _systemConfig: ConfigService,
        private readonly _httpClient: HttpClient,
        private readonly _scheduleService: ScheduleService,
        @Inject(SESSION_STORAGE) private readonly _sessionStorage: WebStorageService) {

        this.reset();
    }

    reset() {
        this.appointment = {
            trn: '',
            qrCode: '',
            serviceType: ServiceType.NewBusiness,
            applicant: {
                idType: null,
                id: '',
                surname: ''
            },
            appointmentDate: null,
            startTime: null,
            endTime: null,
            locationEn: '',
            locationZh: '',
            registrations: []
        };
    }

    enroll(request: EnrollRequest): Observable<unknown> {
        const observable = new Observable<unknown>((observer) => {
            this._httpClient.post(
                this._systemConfig.getSettings<string>('system.hostApi') + '/services/AppointmentManagement/Registration/enroll',
                request,
                {
                    headers: HEADERS_AJAX_JSON_REQUEST
                }
            ).subscribe({
                next: (response: EnrollResponse) => {
                    if (response.resultCode === RESULT_CODE_SUCCESS) {
                        this.appointment.applicant = request.applicant;
                        this._sessionStorage.set(SESSION_DATA_KEY_JWT, response.userToken);
                        this._scheduleService.setSchedules(response.schedules);
                        observer.next();
                    } else {
                        observer.error(response.resultCode);
                    }
                },
                error: (error: HttpErrorResponse) => {
                    observer.error('' + error.status);
                }
            });
        });
        return observable;
    }

    submit(): Observable<unknown> {
        const request: SubmitRegistrationRequest = {
            registrations: this.appointment.registrations
        };

        const observable = new Observable<unknown>((observer) => {
            this._httpClient.post(
                this._systemConfig.getSettings<string>('system.hostApi') + '/services/AppointmentManagement/Registration/submit',
                request,
                {
                    headers: HEADERS_AJAX_JSON_REQUEST
                }
            ).subscribe({
                next: (response: SubmitRegistrationResponse) => {
                    if (response.resultCode === RESULT_CODE_SUCCESS) {
                        this.appointment = response.appointment;
                        observer.next();
                    } else {
                        observer.error(response.resultCode);
                    }
                },
                error: (error: HttpErrorResponse) => {
                    observer.error('' + error.status);
                }
            });
        });
        return observable;
    }

}
